<template>
    <div id="AaddNewArticle">
         <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="40%"
        :before-close="handleClose">
            <el-form ref="form" :model="form" label-width="80px">
                <el-form-item label="文章标题">
                    <el-input v-model="form.title"></el-input>
                </el-form-item>
                <el-form-item label="发布日期">
                     <el-col :span="6">
                        <el-date-picker
                            v-model="form.pubDate"
                            type="datetime"
                            placeholder="选择日期时间"
                            default-time="12:00:00">
                        </el-date-picker>
                    </el-col>
                </el-form-item>
                <el-form-item label="发布者">
                    <el-col :span="6">
                        <el-input v-model="form.authore"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label="文章类型">
                    <el-col>
                        <el-radio-group v-model="form.category" size="mini">
                            <el-radio-button v-for="(item,key) in allCategory.data"  :label="item.name" :key="key"></el-radio-button>
                        </el-radio-group>
                    </el-col>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="saveArticle()" v-if="isAddNew">确 定</el-button>
                <el-button type="primary" @click="updateArticle()" v-else>更新</el-button>
            </span>
        </el-dialog>
            <mt-editor v-model="form.content" :config="edConfig" height="90%" width="98%"></mt-editor> 
            <div style="width:100%;text-align:right;margin-top:10px">
                <button class="btn danger" @click="$router.push({name:'articleList'})">取消</button>
                <button class="btn primary" @click="dialogVisible=true">提交</button>
            </div>
        
    </div>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
    data(){
        return {
            message: '',
            edConfig:[
                {"command":"image","title":'图片'},
                {"command":"table","title":'表格'},
                {"command":"link","title":'链接'},
                {"command":"bold","title":"加粗"},
                {"command":"italic","title":"斜体"},
                {"command":"underline","title":"下划线"},
                {"command":"strikethrough","title":"删除线"},
                {"command":"justifyLeft","title":"居左"},
                {"command":"justifyCenter","title":"居中"},
                {"command":"justifyRight","title":"居右"},
                {"command":"justifyFull","title":"两侧"},
                {"command":"InsertUnorderedList","title":"无序列表"},
                {"command":"insertOrderedList","title":"有序列表"},
                {"command":"superscript","title":"上标"},
                {"command":"subscript","title":"下标"},
                {"command":"code","title":"代码"},
                {"command":"removeFormat","title":"擦除"},
                {"command":"undo","title":"撤销"},
                {"command":"redo","title":"重做"},
            ],
            dialogVisible:false,
            form: {
                //  要上传的内容
                title: '',
                pubDate: new Date(),
                authore: 'zmt',
                content: '',
                articleType: '',
                isPublished:true,
                category:''
            },
            categories:[],
            isAddNew:true
        }
    },
    computed: {
        ...mapGetters(['allCategory'])
    },
    methods:{
        saveArticle(){
            this.dialogVisible=false;
            this.post(this.$apis.addNewArticle,this.form);
            this.$router.push({name:'articleList'})
        },
        handleClose(){
            this.dialogVisible = false;
        },
        updateArticle(){
            this.post(this.$apis.updateArticle,this.form);
            this.$router.push({name:'articleList'})
        }
    },
    mounted() {
        if(this.$route.params.isUpdate){
            this.isAddNew =false;
            if(this.$route.params.article){
                this.form = this.$route.params.article;
            }else{
                this.$router.push({name:'articleList'})
            }
        }
    },
}
</script>
<style lang="scss" scoped>
    #AaddNewArticle{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;

    }
</style>
